.slide {
	color: $white;

	&__head {
		background-color: $violet;
		padding: 15px 20px;
		display: flex;
		align-items: center;
		border-radius: 20px;
		box-shadow: 0 42px 60px -30px $violet;
		@include sm {
			box-shadow: 0 42px 60px -32px $violet; } }


	&__photo {
		width: 90px;
		height: 90px;
		border-radius: 50%;
		border: 2px $white solid;
		object-fit: cover;
		object-position: center;
		font-style: 0; }


	&__text {
		text-align: left;
		margin-left: 20px; }

	&__name {
		letter-spacing: 0.033em;
		line-height: 1;
		font-size: 21px;
		font-weight: 700;
		@include sm {
			font-size: 16px; } }

	&__position {
		@include sm {
			font-size: 14px; } }

	&__feedback {
		margin-top: 31px;
		padding: 30px;
		position: relative;
		background-color: $violet;
		border-radius: 40px;
		box-shadow: 0 42px 60px -30px $violet;
		&:before {
			position: absolute;
			content: '';
			background: url("../img/feedback-arrow.svg");
			width: 32px;
			height: 30px;
			display: block;
			top: -20px;
			left: 64px; }
		@include sm {
			box-shadow: 0 42px 60px -38px $violet; } }


	&__feedback-text {
		letter-spacing: 0.022em;
		text-align: left;
		font-size: 18px;
		line-height: 1.8;
		@include sm {
			font-size: 16px; } } }
