[data-modal] {
	position: fixed;
	z-index: -1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	visibility: hidden;
	min-width: 320px;
	// +vertical
	@include clr;
	@include gpu;
	overflow: auto;
	transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
	&.is-open {
		opacity: 1;
		visibility: visible;
		z-index: 100; } }

[data-modal-container] {
	position: relative;
	z-index: 1;
	font-size: 14px;
	float: right;
	// vertical-align: middle
 }	// display: inline-block
[data-modal-close] {
	position: absolute;
	background-color: transparent;
	font-size: 0;
	.icon {
		font-size: 16px; } }

.modal {
	overflow-x: hidden;
	color: $black;
	background-color: rgba($black, .2);
	@include sm {
 }		// padding: 20px
	&__close {
		top: 53px;
		right: 35px;
		width: 40px;
		height: 40px;
		@include sm {
			right: 8px;
			top: 8px; } }
	&__container {
		display: flex;
		transition: transform 750ms;
		transform: translateX(100%);
		padding: 0 125px;
		max-width: 600px;
		width: 100%;
		background-image: url('../img/modalform-bg.svg');
		background-color: $white;
		background-position: left bottom;
		background-repeat: no-repeat;
		height: 100vh;
		justify-self: end;
		.is-open & {
			transform: translateX(0); }
		@include sm {
			max-width: none;
			min-height: 100%;
			height: auto;
			padding: 15px; }
		&_sm {
			padding: 0 76px; } }
	&__inner {
		width: 100%;
		min-height: 100%;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center; }
	&__wrap {
		display: inline-block;
		vertical-align: middle; }
	&__title {
		font-size: 30px;
		font-weight: 900;
		letter-spacing: normal; }
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 40px;
		img {
			display: block;
			max-width: 100%; } }
	&__text {
		font-size: 21px; }
	&__wrap {
		vertical-align: middle;
		display: inline-block;
		padding: 15px;
		@include xs {
			width: 100%;
			max-width: 450px; } } }
