.accordeon {
	// &:last-child
	//     box-shadow: 0 42px 60px -30px $violet
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between;
		color: $white;
		background-color: $violet;
		padding: 23px 30px 28px;
		cursor: pointer;
		@include sm {
			padding: 10px 15px; } }

	&__subtitle {
		font-weight: 700; }

	&__title {
		max-width: 625px;
		letter-spacing: 0.032em;
		line-height: 1.35;
		font-size: 21px;
		font-weight: 900;
		@include sm {
			font-size: 15px;
			line-height: 1.3; } }

	&__icon {
		flex-shrink: 0;
		border: 1px solid $white;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 9px;
		margin-left: 15px;
		@include circle(28);
		@include sm {
			margin-left: 10px; }

		.icon {
			display: block;
			transition: transform .3s;

			.accordeon__head.is-active & {
				transform: rotate(180deg); } } }

	&__body {
		font-weight: 700;
		letter-spacing: 0;
		padding: 30px 30px 55px 30px;
		font-size: 18px;
		line-height: 1.389;
		display: none;
		@include md {
			font-size: 14px; }
		@include xs {
			padding: 15px; }

		p {
			&:not(:first-child) {
				margin-top: 25px;
				@include xs {
					margin-top: 15px; } } } } }
