@media only screen and (max-width: 599px) { .sw-bottom__inner { margin-bottom: 4px; } }

.sw-footer { display: block; position: relative; width: 32px; height: 3px; transform-origin: left; background: #fff; border: none; transition: transform 0.5s, background-color .5s; }

.header.is-fixed .sw-footer, .header.is-open .sw-footer { background-color: #363636; }

