.what-get {
	padding: 165px 0 154px;
	@include md {
		padding: 60px 0; }
	&__title {
		margin-bottom: 18px; }

	&__list {
		margin: 0 -15px;
		display: flex;
		align-items: center;
		// justify-content: space-between
		flex-wrap: wrap;
		@include md {
			justify-content: center; }
		@include sm {
			flex-direction: column;
			align-items: flex-start; } }

	&__item {
		padding: 0 15px;
		flex: 0 0 33.3%;
		max-width: 33.3%;
		display: flex;
		align-items: center;
		margin-top: 25px;

		@include md {
			margin: 25px 0 0; }
		@include sm {
			margin: 25px 0 0;
			flex: 0 0 100%;
			max-width: 100%; } }

	&__item-position {
		flex-shrink: 0; }

	&__item-text {
		font-weight: 700;
		max-width: 240px;
		margin-left: 20px;
		letter-spacing: 0.034em;
		@include sm {
			max-width: none; } }

	&__particle-first {
		left: -99px;
		top: 55px; }

	&__particle-second {
		left: -29px;
		top: -114px; }

	&__particle-third {
		left: 631px;
		top: 53px; }

	&__particle-fourth {
		right: -104px;
		bottom: -35px; } }
